body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
}

a {
  text-decoration: none;
  color: inherit;
}

.header {
  background-color: #007bff;
  color: #fff;
  text-align: center;
}

.header > h2 {
  margin: 0 auto;
}

.container {
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}


h1 {
  text-align: center;
  margin-bottom: 20px;
}

.articles {
  width: 800px;
  margin: 0 auto;
}

.article {
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 0;
}

.article .open{
  border: none;
}

.article-title {
  cursor: pointer;
  color: #007bff;
  margin: 0;
  padding: 10px;
}

.article-content {
  display: none;
  padding: 10px;
}

.article.open .article-content {
  display: block;
}

.list {
  list-style: disc;
  margin-top: 10px;
  padding-left: 20px;
}

.list li {
  margin-bottom: 5px;
}

.section-header {
  font-size: 18px;
}

.close-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  position: relative;
  top: 100px;
  float: right;
  left: 50px;
}

.close-button svg {
  width: 100%;
  height: 100%;
  transform: scale(2);
}

.running-text-container {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.running-text-content {
  white-space: nowrap;
  display: inline-block;
}
